  }
  // let scale = devicewidth / 1920 // 分母——设计稿的尺寸
  // document.body.style.zoom = scale//放大缩小相应倍数
};
onMounted(() => {
  bodyScale();
  window.onresize = () => {
    return (() => {
      bodyScale();
    })();
  };
});
